import { css } from "emotion";
import Link from "next/link";

const Footer = () => {
  const baseStyle = css`
    background-color: #000;
    color: #fff;
    padding-bottom: 1rem;
    > hr {
      width: 83%;
      margin-top: -1.8rem;
      border-bottom: 0.5px solid rgba(255, 255, 255, 0.5);
    }
    > span {
      text-align: center;
      font-size: 0.8rem;
    }
    .wrapper {
      display: grid;
      padding: 3rem 0;
      grid-template-columns: repeat(3, 30%);
      justify-content: center;
      box-sizing: border-box;
      .logo {
        width: 50%;
        padding: 1.3rem 0 0 3rem;
      }

      .linkOne,
      .linkTwo {
        display: flex;
        ul {
          padding: 0;
          margin-right: 4rem;
          li {
            list-style: none;
            margin-bottom: 0.6rem;
            a {
              text-decoration: none;
              color: #fff;
              font-size: 0.9rem;
            }
          }
        }
      }

      .title {
        color: rgba(255, 255, 255, 0.5);
        hr {
          width: 20%;
          margin-left: 0;
          border-bottom: 1px solid rgba(255, 255, 255, 0.5);
        }
      }

      @media (max-width: 700px) {
        grid-template-columns: 1fr;
        .title {
          text-align: center;
          hr {
            margin: auto;
          }
        }

        .logo {
          margin: auto;
          padding: 0;
          width: 36%;
        }

        .linkOne,
        .linkTwo {
          justify-content: center;
        }
        .linkOne {
          ul {
            margin: 2rem;
          }
        }
        .linkTwo {
          flex-direction: column;
          text-align: center;
          ul {
            margin: 0;
            margin-top: 1rem;
          }
        }
      }
    }
  `;

  return (
    <>
      <div className={baseStyle}>
        <div className="wrapper">
          <div className="logo">
            <img src="/static/logo_kejari_wb.svg" alt="Logo Kejari" />
          </div>
          <div>
            <div className="title">
              <h2>Kejaksaan Negeri Barru</h2>
              <hr />
            </div>
            <div className="linkOne">
              <ul>
                <li>
                  <Link href="/">
                    <a>Beranda</a>
                  </Link>
                </li>
                <li>
                  <Link href="/">
                    <a>Sarana</a>
                  </Link>
                </li>
                <li>
                  <Link href="/">
                    <a>Profil</a>
                  </Link>
                </li>
                <li>
                  <Link href="/">
                    <a>Organisasi</a>
                  </Link>
                </li>
              </ul>

              <ul>
                <li>
                  <Link href="/">
                    <a>Berita</a>
                  </Link>
                </li>
                <li>
                  <Link href="/">
                    <a>Galeri</a>
                  </Link>
                </li>
                <li>
                  <Link href="/">
                    <a>Kegiatan</a>
                  </Link>
                </li>
                <li>
                  <Link href="/">
                    <a>Layanan</a>
                  </Link>
                </li>
              </ul>
            </div>
          </div>
          <div>
            <div className="title">
              <h2>Situs Terkait Lainnya</h2>
              <hr />
            </div>
            <div className="linkTwo">
              <ul>
                <li>
                  <Link href="/">
                    <a>Kejaksaan Agung</a>
                  </Link>
                </li>
                <li>
                  <Link href="/">
                    <a>Mahkamah Agung</a>
                  </Link>
                </li>
                <li>
                  <Link href="/">
                    <a>Polri</a>
                  </Link>
                </li>
                <li>
                  <Link href="/">
                    <a>KPK</a>
                  </Link>
                </li>
              </ul>

              <ul>
                <li>
                  <Link href="/">
                    <a>Kejaksaan Tinggi Sulsel</a>
                  </Link>
                </li>
                <li>
                  <Link href="/">
                    <a>Pengadilan Negeri Barru</a>
                  </Link>
                </li>
                <li>
                  <Link href="/">
                    <a>Jaksa Menyapa</a>
                  </Link>
                </li>
                <li>
                  <Link href="/">
                    <a>Link Lainnya</a>
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <hr />
        <span>
          <p>Kejari Barru 2018 - Dev by DTC</p>
        </span>
      </div>
    </>
  );
};

export default Footer;
